import { useCallback, useEffect, useState } from "react";
import { toast } from "react-hot-toast";
import { WeeklyScheduleService } from "../services/api";
import { formatDateForAPI } from "../utils/dateUtils";
import {
  parseScheduleFromApi,
  prepareScheduleForApi,
  standardizeScheduleData,
} from "../utils/scheduleUtils";
import useWebSocket from "./useWebSocket";

/**
 * Hook personnalis√© pour g√©rer les plannings hebdomadaires
 */
const useWeeklySchedules = () => {
  const [schedules, setSchedules] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Int√©gration WebSocket pour les mises √† jour en temps r√©el
  const { socket, isConnected, notifyDataChange } = useWebSocket();

  // √âcouter les mises √† jour WebSocket
  useEffect(() => {
    if (socket && isConnected) {
      // Fonction pour traiter les messages WebSocket
      const handleWebSocketMessage = (event) => {
        try {
          const data = JSON.parse(event.data);

          // Si le message concerne une mise √† jour de planning
          if (data.type === "SCHEDULE_UPDATED" && data.schedule) {
            setSchedules((prevSchedules) => {
              // V√©rifier si le planning existe d√©j√†
              const exists = prevSchedules.some(
                (schedule) => schedule.id === data.schedule.id
              );

              if (exists) {
                // Mettre √† jour le planning existant
                return prevSchedules.map((schedule) =>
                  schedule.id === data.schedule.id
                    ? parseScheduleFromApi(data.schedule)
                    : schedule
                );
              } else {
                // Ajouter le nouveau planning
                return [...prevSchedules, parseScheduleFromApi(data.schedule)];
              }
            });

            toast.info("Planning mis √† jour en temps r√©el");
          }

          // Si le message concerne une suppression de planning
          if (data.type === "SCHEDULE_DELETED" && data.scheduleId) {
            setSchedules((prevSchedules) =>
              prevSchedules.filter(
                (schedule) => schedule.id !== data.scheduleId
              )
            );

            toast.info("Planning supprim√© en temps r√©el");
          }
        } catch (error) {
          console.error(
            "Erreur lors du traitement du message WebSocket:",
            error
          );
        }
      };

      // Ajouter l'√©couteur d'√©v√©nements
      socket.addEventListener("message", handleWebSocketMessage);

      // Nettoyer l'√©couteur lors du d√©montage
      return () => {
        socket.removeEventListener("message", handleWebSocketMessage);
      };
    }
  }, [socket, isConnected]);

  /**
   * R√©cup√®re tous les plannings pour une semaine donn√©e
   * @param {string} weekStart - Date de d√©but de semaine (format YYYY-MM-DD)
   * @returns {Promise<Array>} - Liste des plannings
   */
  const fetchSchedules = useCallback(async (weekStart) => {
    setLoading(true);
    setError(null);

    try {
      // V√©rifier que la date est au bon format
      if (!weekStart) {
        throw new Error("Date de d√©but de semaine non sp√©cifi√©e");
      }

      // V√©rifier que le token d'authentification est pr√©sent
      const token = localStorage.getItem("token");
      if (!token) {
        console.error("Token d'authentification manquant");
        throw new Error("Vous devez √™tre connect√© pour acc√©der √† ces donn√©es");
      }

      // S'assurer que la date est au format YYYY-MM-DD
      let formattedDate = weekStart;
      if (weekStart instanceof Date) {
        formattedDate = formatDateForAPI(weekStart);
      } else if (typeof weekStart === "string" && weekStart.includes("T")) {
        // Si la date contient un T (format ISO), extraire seulement la partie date
        formattedDate = weekStart.split("T")[0];
      }

      console.error(
        "Tentative de r√©cup√©ration des plannings pour la semaine du:",
        formattedDate
      );

      try {
        const result = await WeeklyScheduleService.getByWeek(formattedDate);

        if (!result.success) {
          console.error(
            "√âchec de la r√©cup√©ration des plannings:",
            result.message,
            result.details || ""
          );
          throw new Error(
            result.message || "Erreur lors du chargement des horaires"
          );
        }

        // Standardiser les donn√©es
        const standardizedSchedules = result.schedules.map((schedule) =>
          parseScheduleFromApi(schedule)
        );

        setSchedules(standardizedSchedules);
        return standardizedSchedules;
      } catch (apiError) {
        console.error(
          "Erreur API lors de la r√©cup√©ration des plannings:",
          apiError
        );
        throw apiError;
      }
    } catch (err) {
      console.error("Erreur lors du chargement des horaires:", err);
      console.error("D√©tails de l'erreur:", JSON.stringify(err, null, 2));
      setError(
        "Erreur lors du chargement des horaires: " +
          (err.message || "Erreur inconnue")
      );
      toast.error("Erreur lors du chargement des horaires");
      return [];
    } finally {
      setLoading(false);
    }
  }, []);

  /**
   * Cr√©e un nouveau planning
   * @param {Object} scheduleData - Donn√©es du planning
   * @returns {Promise<Object>} - R√©sultat de la cr√©ation
   */
  const createSchedule = useCallback(
    async (scheduleData) => {
      setLoading(true);
      try {
        // Standardiser et pr√©parer les donn√©es pour l'API
        const apiData = prepareScheduleForApi(
          standardizeScheduleData(scheduleData)
        );

        const result = await WeeklyScheduleService.create(apiData);

        if (!result.success) {
          throw new Error(
            result.message || "Erreur lors de la cr√©ation de l'horaire"
          );
        }

        // Ajouter le nouveau planning √† la liste
        const newSchedule = parseScheduleFromApi(result.schedule);
        setSchedules((prev) => [...prev, newSchedule]);

        // Notifier les autres clients via WebSocket
        notifyDataChange("schedule", "create", newSchedule.id);

        toast.success("Planning cr√©√© avec succ√®s");
        return { success: true, schedule: newSchedule };
      } catch (error) {
        console.error("Erreur lors de la cr√©ation du planning:", error);
        toast.error(error.message || "Erreur lors de la cr√©ation du planning");
        return { success: false, error: error.message };
      }
    },
    [notifyDataChange]
  );

  /**
   * Met √† jour un planning existant
   * @param {number} id - ID du planning
   * @param {Object} scheduleData - Donn√©es du planning
   * @returns {Promise<Object>} - R√©sultat de la mise √† jour
   */
  const updateSchedule = useCallback(
    async (id, scheduleData) => {
      setLoading(true);
      try {
        // Standardiser et pr√©parer les donn√©es pour l'API
        const apiData = prepareScheduleForApi(
          standardizeScheduleData(scheduleData)
        );

        const result = await WeeklyScheduleService.update(id, apiData);

        if (!result.success) {
          throw new Error(
            result.message || "Erreur lors de la mise √† jour de l'horaire"
          );
        }

        // Mettre √† jour le planning dans la liste
        const updatedSchedule = parseScheduleFromApi(result.schedule);
        setSchedules((prev) =>
          prev.map((schedule) =>
            schedule.id === id ? updatedSchedule : schedule
          )
        );

        // Notifier les autres clients via WebSocket
        notifyDataChange("schedule", "update", id);

        toast.success("Planning mis √† jour avec succ√®s");
        return { success: true, schedule: updatedSchedule };
      } catch (error) {
        console.error("Erreur lors de la mise √† jour du planning:", error);
        toast.error(
          error.message || "Erreur lors de la mise √† jour du planning"
        );
        return { success: false, error: error.message };
      }
    },
    [notifyDataChange]
  );

  /**
   * Supprime un planning
   * @param {number} id - ID du planning
   * @returns {Promise<Object>} - R√©sultat de la suppression
   */
  const deleteSchedule = useCallback(
    async (id) => {
      setLoading(true);
      try {
        const result = await WeeklyScheduleService.delete(id);

        if (!result.success) {
          throw new Error(
            result.message || "Erreur lors de la suppression de l'horaire"
          );
        }

        // Supprimer le planning de la liste
        setSchedules((prev) => prev.filter((schedule) => schedule.id !== id));

        // Notifier les autres clients via WebSocket
        notifyDataChange("schedule", "delete", id);

        toast.success("Planning supprim√© avec succ√®s");
        return { success: true };
      } catch (error) {
        console.error("Erreur lors de la suppression du planning:", error);
        toast.error(
          error.message || "Erreur lors de la suppression du planning"
        );
        return { success: false, error: error.message };
      }
    },
    [notifyDataChange]
  );

  return {
    schedules,
    loading,
    error,
    fetchSchedules,
    createSchedule,
    updateSchedule,
    deleteSchedule,
  };
};

export default useWeeklySchedules;
