import { useCallback, useState } from "react";
import { toast } from "react-hot-toast";
import { HourBalanceService } from "../services/api";

/**
 * Hook personnalis√© pour g√©rer le solde d'heures des employ√©s
 */
const useHourBalance = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  /**
   * R√©cup√®re le solde d'heures d'un employ√©
   * @param {number} employeeId - ID de l'employ√©
   * @returns {Promise<Object>} - Solde d'heures de l'employ√©
   */
  const getEmployeeBalance = useCallback(async (employeeId) => {
    setLoading(true);
    setError(null);

    try {
      const result = await HourBalanceService.getByEmployee(employeeId);

      if (!result.success) {
        console.warn(
          `Avertissement: Impossible de r√©cup√©rer le solde d'heures pour l'employ√© ${employeeId}:`,
          result.message
        );
        return 0; // Retourner 0 par d√©faut au lieu de lancer une erreur
      }

      return result.balance;
    } catch (err) {
      console.warn(
        `Avertissement: Erreur lors de la r√©cup√©ration du solde d'heures pour l'employ√© ${employeeId}:`,
        err
      );
      setError(
        err.message || "Erreur lors de la r√©cup√©ration du solde d'heures"
      );
      // Ne pas afficher de toast d'erreur pour ne pas perturber l'utilisateur
      return 0; // Retourner 0 par d√©faut
    } finally {
      setLoading(false);
    }
  }, []);

  /**
   * Met √† jour le solde d'heures d'un employ√©
   * @param {number} employeeId - ID de l'employ√©
   * @param {Object} balanceData - Donn√©es du solde d'heures
   * @returns {Promise<Object>} - R√©sultat de la mise √† jour
   */
  const updateEmployeeBalance = useCallback(async (employeeId, balanceData) => {
    setLoading(true);
    setError(null);

    try {
      const result = await HourBalanceService.updateBalance(
        employeeId,
        balanceData
      );

      if (!result.success) {
        throw new Error(
          result.message || "Erreur lors de la mise √† jour du solde d'heures"
        );
      }

      toast.success("Solde d'heures mis √† jour avec succ√®s");
      return { success: true, balance: result.balance };
    } catch (err) {
      console.error(
        `Erreur lors de la mise √† jour du solde d'heures pour l'employ√© ${employeeId}:`,
        err
      );
      setError(
        err.message || "Erreur lors de la mise √† jour du solde d'heures"
      );
      toast.error("Erreur lors de la mise √† jour du solde d'heures");
      return { success: false, error: err.message };
    } finally {
      setLoading(false);
    }
  }, []);

  return {
    loading,
    error,
    getEmployeeBalance,
    updateEmployeeBalance,
  };
};

export default useHourBalance;
